/*.............CSS reset.........*/
*, *::before, *::after {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
}

a {
  text-decoration: none;
}

ul,
ol,
li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}

html,
body {
  height: 100%;
  line-height: 1;
  font-size: 100%;
  color: #000;
  -webkit-font-smoothing: antialiased;
}

.wrapper {
  min-height: 100%;
  overflow: hidden;
}

body {
  margin: 0;
  padding: 0;
  background-color: var(--background-colour);
  color: var(--text-colour-dark);
}

.container {
  width: min(100% - 40px, 1400px);
  margin-inline: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

:root {
  --background-colour: #F3EDCF;
  --primary-colour: #4d3a7a;
  --accent-colour: #D1FFBD;
  --text-colour-dark: #111;
  --text-colour-light: #F3EDCF;
}

.header {
  min-height: 100vh;
  width: 100%;
  background-image: linear-gradient(rgba(243, 237, 207, 0.6), rgba(243, 237, 207, 0.6)), url(/app/img/banner2.jpg);
  background-position: top;
  background-size: cover;
  position: relative;
}
.header__nav .nav__logo {
  font-size: 2rem;
  color: crimson;
  font-weight: bold;
}
.header__nav .nav__links {
  flex: 1;
  text-align: right;
}
.header__nav .nav__links li {
  display: inline-block;
  padding: 10px 11px;
  position: relative;
}
.header__nav .nav__links a {
  color: var(--text-colour-dark);
  font-size: 1.3rem;
}
.header__nav .nav__links a:hover {
  color: var(--primary-colour);
}